A,cACA,gBACA,aAAc;CApBlB;AAAA,2BAuBI,qBACA,kBACA,cACA,UAAW;CA1Bf;AAAA,6BA6BI,UACA,gCACA,aAAe;CA/BnB;AAAA,kCAiCM,uBACA,SAAU;CAlChB;AAAA,8BAsCI,UACA,eACA,iBACA,QACA,kCAA8B;CA1ClC;AAAA,0BA6CI,WACA,8BAAA,qBAAsB;CA9C1B;AAAA,6BAgDM,UACA,kBACA,UACA,gCACA,cACA,mBACA,eAAgB;CAtDtB;AAAA,8BAyDM,UACA,cACA,kBACA,YACA,uBAAmB;CACpB;AAAA,gBAMD,gCACA,4BAAwB;CAH5B;AAAA,gBAMI,qBACA,kBACA,cACA,UAAW;CATf;AAAA,iBAYI,oBAAqB;CExlBzB;AAAA,YACE,sBACA,0DAAA,kDACA,WACA,YACA,kBACA,mCAAA,2BACA,yBAAA,iBACA,8BAAA,qBAAsB;CACvB;AAAA,wCAGC,UACA,mCAAA,0BAA2B;CAC5B;AAAA,wCAGC,UACA,oCAAA,2BAA4B;CAC7B;AAAA,sGAMC,wBAAA,gBACA,kBACA,KAAM;CACP;AAAA,UAGC,SACA,UACA,YACA,WACA,kBACA,kCACA,kBACA,aAAe;CATjB;AAAA,cAWI,oBAAqB;CAXzB;AAAA,cAcI,SACA,SAAU;CAfd;AAAA,oBAkBI,mDACA,mCACA,kCACA,YACA,iBAAkB;CAtBtB;AAAA,8DAwBM,YACA,yCAAA,gCAAiC;CAClC;AAAA;AA1BL,8DAgCQ,YACA,yCAAA,gCAAiC;CAClC;CAIL;AAAA;AAtCF,8DAyCQ,yCAAA,gCAAiC;CAClC;CAIL;AAAA;AA9CF,8DAiDQ,yCAAA,gCAAiC;CAClC;CAIL;AAAA;AAtDF,8DAyDQ,yCAAA,gCAAiC;CAClC;CAIL;AAAA;AA9DF,8DAiEQ,yCAAA,gCAAiC;CAClC;CAIL;AAAA;AAtEF,8DAyEQ,yCAAA,gCAAiC;CAClC;CAAA","file":"App.vue","sourcesContent":["@import \"theme.scss\";\r\n/*vant轻提示全局样式 */\r\n.van-toast {\r\n  min-width: 250px;\r\n  line-height: 50px;\r\n  font-size: 30px;\r\n  .van-toast__icon {\r\n    font-size: 100px;\r\n  }\r\n}\r\n/* 全局隐藏滚动条 */\r\nul::-webkit-scrollbar{\r\n  display: none;\r\n}\r\n/* 烟包支付 */\r\n.packagepayment {\r\n  width: 100%;\r\n  height: 100%;\r\n  overflow: hidden;\r\n  position: relative;\r\n  .slide {\r\n    width: 100%;\r\n    height: 100%;\r\n    overflow-y: auto;\r\n  }\r\n  .weight {\r\n    font-weight: 700;\r\n    font-size: 31px;\r\n  }\r\n  /* 包 */\r\n  .vif-package {\r\n    width: 100%;\r\n    .package {\r\n      width: 95%;\r\n      overflow: hidden;\r\n      box-sizing: border-box;\r\n      display: flex;\r\n      flex-wrap: nowrap;\r\n      justify-content: space-between;\r\n      align-items: center;\r\n      margin: 40px auto;\r\n      position: relative;\r\n      .icon-color {\r\n        posi